<template>
    <div class="accordion-faq wow move-up">
        <b-card no-body>
            <b-card-header header-tag="header">
                <b-button block v-b-toggle.accordion-1 class="faq-title">What is a Cardano Stake Pool?</b-button>
            </b-card-header>
            <b-collapse id="accordion-1" visible accordion="my-accordion">
                <b-card-body>
                    <b-card-text>
                         A Cardano stake pool is a node or server on the Cardano network that acts on behalf of stakeholders. It is usually set up and run by an individual or entity with the requisite resources, technical skills, and time.
Those with no technical capacity to set up their own nodes may choose to delegate their stake to a stake pool. In return, the delegators receive a share of the reward it earns.
                    </b-card-text>
                </b-card-body>
            </b-collapse>
        </b-card>

        <b-card no-body>
            <b-card-header header-tag="header">
                <b-button block v-b-toggle.accordion-2 class="faq-title">How to stake Cardano (ADA) ?</b-button>
            </b-card-header>
            <b-collapse id="accordion-2" accordion="my-accordion">
                <b-card-body>
                    <b-card-text>
                        <p>First buy your coins at any exchange you like. Then you need to send your ADA to one of the oficial cardano wallets.</p>
                        <p>Staking from Yoroi wallet on Google Chrome:</p>
                        <ul class="mb-5">
                            <li>1. Download the Yoroi Light Wallet at <a href="https://yoroi-wallet.com/#/" target="_Blank">yoroi-wallet.com</a>.</li>
                            <li>2. Choose your preferred browser option after clicking the download button.</li>
                            <li>3. After adding it to your browser, launch the Yoroi wallet extension within the browser.</li>
                            <li>4. Follow the instructions to connect/create/restore your Cardano wallet.</li>
                            <li>5. Go to the “Delegation list” tab, and search for “ADAZUL”.</li>
                            <li>6. Select the Adazul pool (ADABR) and click the “Delegate” button.</li>
                            <li>
                                * You may now go to the “Dashboard” tab to track your rewards.
                                * Please wait 15-20 days for your first rewards to show up.
                            </li>
                        </ul>

                        <p>Staking from Daedalus wallet:</p>
                        <ul class="mb-5">
                            <li>1. Download the Daedalus Wallet at <a href="https://daedaluswallet.io/" target="_Blank">daedaluswallet.io</a>.</li>
                            <li>2. The first time launching the wallet, you will need to wait for Daedalus to sync with the blockchain.</li>
                            <li>3. After Daedalus finished syncing, follow the instructions to connect/create/restore your wallet.</li>
                            <li>4. Click the second button in the left panel and select the “Stake Pools” tab.</li>
                            <li>5. Search for “ADAZUL” and select the pool. (Ticker: ADABR)</li>
                            <li>6. Click the “Delegate to this pool” button.</li>
                            <li>
                            * You may now go to the “Dashboard” tab to track your rewards.
                            * Please wait 15-20 days for your first rewards to show up.
                            </li>
                        </ul>

                        <p>Staking from Mobile (Android or IOS):</p>
                        <ul>
                            <li>1. Go and download the Yoroi mobile app for your device: Android | iOS (Beware of SCAM APPS on Android. Make sure is from EMURGO)</li>
                            <li>2. Launch the app and follow the instructions to connect/create/restore your Cardano wallet.</li>
                            <li>3. Once set up, go to the “Dashboard” tab and click “Go to Staking Center”.</li>
                            <li>4. Search for ADAZUL and select the pool.</li>
                            <li>5. Click the “Delegate” button.</li>
                            <li>
                                * You may now go to the “Dashboard” tab to track your rewards.
                                * Please wait 15-20 days for your first rewards to show up.
                            </li>
                        </ul>
                        
                        
                    
                    </b-card-text>
                </b-card-body>
            </b-collapse>
        </b-card>

        <b-card no-body>
            <b-card-header header-tag="header">
                <b-button block v-b-toggle.accordion-3 class="faq-title">How Long Does it Take to Receive my First Rewards?</b-button>
            </b-card-header>
            <b-collapse id="accordion-3" accordion="my-accordion">
                <b-card-body>
                    <b-card-text>
                        <p>For any change in delegation, the rewards are reflected after 4 epoch transitions (20 days).</p> 
                        <p>For example, if you staked in epoch X, your first reward will show up at the start of X+4.</p>
                         After the initial wait time, you can expect to receive rewards at the end of every epoch (5 days).
                    </b-card-text>
                </b-card-body>
            </b-collapse>
        </b-card>

        <b-card no-body>
            <b-card-header header-tag="header">
                <b-button block v-b-toggle.accordion-4 class="faq-title">What is the Fixed Fee, and How Is It Calculated?</b-button>
            </b-card-header>
            <b-collapse id="accordion-4" accordion="my-accordion">
                <b-card-body>
                    <b-card-text>
                        <p> The fixed fee is an amount of ADA taken from the pool’s total rewards in an epoch. This is not a fee charged to each delegator. It's a protocol fixed fee, the minimum a pool can set is 340. </p>
                        <p> For example, suppose the Adazul pool earned 10000 ADA in the past epoch. The protocol automatically deducts 340 ADA as a fixed fee from the 10000 ADA and sends it to our wallet. Therefore 10000-340=9660 ADA is left.</p>
                        <p> Then variable fee will be calculated and deducted based on the percentage set by the stake pool operator. In the case of Adazul pool, we charge 2%. Thus 9660 x 2%=193.2 ADA.</p>
                        <p> Delegators will be left with 9660-193.2=9466.8 ADA, distributed to each delegator in proportion to the amount of stake delegated.</p>
                        <p> Both the fixed fee and the variable fee are only charged when the pool receives a reward. If the pool does not receive a reward in an epoch, the operator does not receive any fee.</p>
                        
                    </b-card-text>
                </b-card-body>
            </b-collapse>
        </b-card>

        <b-card no-body>
            <b-card-header header-tag="header">
                <b-button block v-b-toggle.accordion-5 class="faq-title">How Many ADA Will I Be Able To Earn?</b-button>
            </b-card-header>
            <b-collapse id="accordion-5" accordion="my-accordion">
                <b-card-body>
                    <b-card-text>
                        <p> The overall annual return on ADA should be around 5% as per current protocol parameters. The amount of ADA you can earn is not fixed, it depends greatly on the following factors:</p>
                        <p> 1. Amount of ADA staked.</p>
                        <p> 2. Performance or luck of the stake pool (Blocks expected Vs Blocks Minted).</p>
                        <p> 3. Percentage of fee the stake pool charges.</p>
                        <p> You can use our rewards calculator to get an idea of how much you will earn in rewards. It’s important to note that the calculator produces only reward estimates and shouldn’t be considered definitive or a guarantee of reward amounts.</p>
                        
                    </b-card-text>
                </b-card-body>
            </b-collapse>
        </b-card>
    </div>
</template>

<script>
    export default {
        name: 'Accordion',
    };
</script>